<template>
<section class="px-3">
  
  <input-components 
  :compaigns='compaigns' 
  :WkPresets="WkPresets"
  :jobTypes='jobTypes'
  :Module='Module'
  :Grade="Grade"
  :curStatusDts='curStatusDts' :frmDataSubmit="frmDataSubmit" :frm="frm"
  ></input-components>
  <filters-table-vue 
  :results='results' :cls="cls" 
  :currentPage="currentPage"
  :rowsPage="rowsPage"
  :totalPages="totalPages"
  :jobID="jobID"
  :openModalDuration="openModalDuration"
  :clickCallback="clickCallback"
  :chk_box="chk_box"
  :delete_temp_data="delete_temp_data"
  :clearTempData="clearTempData"
  :getExportDataPage="getExportDataPage"
  ></filters-table-vue>
  <jobDurationComponent 
  :showModalDuration="showModalDuration" 
  :chartDisplayData="chartDisplayData" 
  ></jobDurationComponent>
</section>
</template>
<script>

import  InputComponents from '@/components/InputComponent.vue';
import  FiltersTableVue from '@/components/FiltersTableVueComponent.vue';
import  jobDurationComponent from '@/components/jobDurationComponent.vue';
import APIS from '@/lib/APIS';
 export default {
    components: {
      InputComponents,
      FiltersTableVue,
      jobDurationComponent
    }, data : () =>({
      chk_box:[],
      showModalDuration: false,
      jobID: '',
      active:'',
      currentPage:1,
      rowsPage:10,
      totalPages:0,
      jobCards:[],
      Grade:[],
      Module:[],
      compaigns:[],
      WkPresets:[],
      jobTypes:[],
      curStatusDts:[],
      frm:[],
      frmobj:[],
      results:[],
      totalRows:0,
      cls:'',
      chartDisplayData : [
        ["Stage Name", "Stage Duration (In Days)", {role:'style'}]
      ]
    }),
    computed:{
     // this.autocompleteItems=  APIS.getCurrentStatus()
     //getJobByCampaignid
    },
    async mounted() {
      this.compaigns= await APIS.getAllCompangns();
      const jobtypes = await APIS.getJobType();
      this.WkPresets=APIS.getPrestsGroup();
      this.jobTypes=jobtypes[0].options
      var dtgrade  =await APIS.getGrade();
      this.Grade =dtgrade[0].options;
      var dtmodule=await APIS.getModule();
      this.Module=dtmodule[0].options;
    },
    methods :{
      async getExportDataPage(){
        var response=await APIS.exportAsExcel(this.frmobj);
        console.log("data is given ", response);
        return response;
        
      },
      clearTempData(data){
      debugger
        for(let dtID in data){
         var indexNo= this.results.findIndex(result=>result.id==data[0]);
         this.results.splice(indexNo , 1);
        }
    },
      delete_temp_data(){
        // eslint-disable-next-line
        console.log(this.chk_box);
      },
      async openModalDuration(jobID){
        this.chartDisplayData=this.newchartDisplayData();
        let dts  =await APIS.getJobDurationData(jobID);
        let color = "";

        if(dts.length > 0 ){
          for(var t=0; t< dts.length; t++){
              if(dts[t] && dts[t].Preset_Stages && dts[t].Preset_Stages.length > 0){
                  for(var k=0; k < dts[t].Preset_Stages.length ; k++){

                    debugger
                      if(dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("research asset and original source")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("select job type")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("copyright status")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("contract negotiation and asset procurement")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("asset approval")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("a&p record keeping")!=-1 ){
                        color="#f3f722";
                      }
                      else if(dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("art production lead assigns designer")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("designer create asset")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("art production lead review")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("waiting room preflight")!=-1 ){
                        color="#14bc63";
                      }
                      else if(dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("search the dam for existing assets")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("verify permissions and upload the clip art asset")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("approve and upload asset to the waiting room")!=-1 ){
                        color="#1f63d1";
                      }else if(dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("complete image research and provide options to writer")!=-1 || 
                      dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("upload final image")!=-1 ){
                        color="#bcb614";
                      }else if(dts[t].Preset_Stages[k].Stage_Name.toLowerCase().indexOf("content team feedback and approval")!=-1 ){
                        color="#a31dc1";
                      }else{
                        color="#b71623";
                      }

                      if(dts[t].Preset_Stages[k].Stage_Name!=""){
                          var Stage_Duration=dts[t].Preset_Stages[k].Stage_Duration;
                          if(Stage_Duration ==""){
                              Stage_Duration=0;
                          } else if( Stage_Duration.indexOf(" days ") !=-1){
                              Stage_Duration=parseInt(Stage_Duration.split(" days ")[0]);
                          }
                          var d=[dts[t].Preset_Stages[k].Stage_Name, Stage_Duration, color ];
                          this.chartDisplayData.push(d);
                      }
                  }
              }
          }
      }
      this.$children[2].chartData=this.chartDisplayData;
      this.$modal.show('hello-world');
      }, newchartDisplayData(){
        return [ ["Stage Name", "Stage Duration (In Days)", {role:'style'}]];
      },
      async clickCallback(pageNum){
        this.frmobj.currentPage=pageNum;
        this.cls="loading";
        debugger
        //this.results=  await APIS.getjobsbycampaignid(this.frmobj);
        var data =  await APIS.getjobsbycampaignid(this.frmobj);
        this.results=data.rows;
        this.totalRows=data.rowCount;
        this.cls="";
      }, async  frmDataSubmit(obj, tags){
        debugger
        var a=[];
        if(tags.length >0){
          for(let i=0; i<tags.length; i++){
            a.push(tags[i].text);
          }
        }
        this.currentPage=1;
        obj.currentPage=this.currentPage;
        obj.rowsPage=this.rowsPage;
        obj.currentStatus=a;
        this.frmobj=obj;
        this.cls="loading";
        var data =  await APIS.getjobsbycampaignid(obj);
        this.results=data.rows;
        this.totalRows=data.rowCount;
        var pages=1;
        if(data.rowCount % this.rowsPage!=0){
          pages=Math.round(data.rowCount/ this.rowsPage,1)+1;
        }else{
          pages=data.rowCount/ this.rowsPage;
        }
        this.totalPages= pages;
        //console.log(data);
        this.cls="";
      },
    }
  }
</script>
<style lang="css" >
  table{font-size: 10px;}
  .loading {
    background: #2a2c2e91;
    opacity: 0.8;
  }
  .page-link.active{
  background: #f3f3f3;
  }
  .disable{

  }
</style>